.bg-container {
  background-color: #ffd569;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.card-container {
  background-color: white;
  padding: 30px;
  border-radius: 10px;
}
.heading {
  font-family: 'Roboto';
  color: black;
  font-size: 26px;
  margin-bottom: 30px;
}
.fruits-container {
  display: flex;
  justify-content: space-around;
}
.fruit-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.image {
  width: 130px;
}
.eat-btn {
  font-family: 'Roboto';
  color: white;
  font-size: 12px;
  background-color: #007bff;
  border-radius: 8px;
  width: 75px;
  height: 25px;
  border: 0px;
  margin: 20px 0;
}
.count {
  color: #ffd569;
}
